.menu__burger {
    @include button-reset;
    display: flex;
    flex-direction: column;
    margin: 10px 15px;
}

.menu__burger--top,
.menu__burger--center,
.menu__burger--bottom {
    margin: 5px;
    width: 65px;
    height: 2px;
    background-color: $pink;
    border: 1px solid $pink;
}

@media (min-width: $tablet-width) {
    .menu__burger {
        margin: 15px 25px;
    }
}