body {
  max-width: 1200px;
  margin: 0 auto; }

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #0c0000;
  border-bottom: 3px solid #f7d2d2; }

.navigation {
  display: none; }

.logo {
  padding: 10px 15px;
  font-size: 32px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  color: #f7d2d2; }

@media (min-width: 660px) {
  .logo {
    padding: 15px 25px;
    font-size: 44px; } }

.menu__burger {
  display: block;
  border: none;
  background: none;
  outline: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 10px 15px; }

.menu__burger--top,
.menu__burger--center,
.menu__burger--bottom {
  margin: 5px;
  width: 65px;
  height: 2px;
  background-color: #f7d2d2;
  border: 1px solid #f7d2d2; }

@media (min-width: 660px) {
  .menu__burger {
    margin: 15px 25px; } }

.slider {
  height: 500px;
  width: 100%;
  margin-bottom: -5px;
  background-image: url(../img/c.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-color: #0c0000; }

.asterisk {
  margin: 0;
  margin-bottom: -2px;
  padding-left: 171px;
  padding-right: 5px;
  background-color: #f7d2d2;
  font-size: 10px;
  line-height: 10px;
  color: #0c0000; }

.sells {
  margin-top: -22px;
  background-color: #f7d2d2; }

.sells__title {
  font-size: 24px;
  font-weight: 400;
  color: #0c0000; }
